* {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: 'Space Mono', monospace;
  font-weight: 400;
  background-color: rgb(255, 245, 237);
}

.container {
  width: 820px;
  min-height: 700px;
  margin: 20px auto;
}

.header {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
}

.header_title {
  margin-right: 550px;

}

.theme_name {
  margin-right: 10px;
  color: rgb(150, 150, 150);
}

.theme_icon {
  fill: rgb(150, 150, 150);
}

.search {
  position: relative;
  width: 100%;
  height: 75px;
  overflow: hidden;
  padding: 10px 10px 10px 16px;
  background-color: white;
  box-shadow:
    0 0 0 2px rgb(255, 255, 255),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.2);
  margin: 20px 0;
  border-radius: 15px;
  display: flex;
  align-items: center;
}

.label {
  cursor: pointer;
}

.input {
  padding: 0 10px;
  flex-grow: 1;
  height: 60px;
  max-width: calc(100% - 100px - 20px);
  border: none;
  font-size: 20px;
  line-height: 1.5;
  cursor: pointer;
}

.input:focus {
  outline: none;
}

.input::placeholder {
  color: lightgray;
}

.search_icon {
  fill: rgb(150, 150, 150);
}

.button {
  padding: 10px 22px;
  border-radius: 10px;
  border: none;
  background-color: rgb(255, 178, 110);
  color: rgb(30, 18, 18);
  font-size: 1rem;
  line-height: 1.5;
  cursor: pointer;
}

.button:hover {
  background-color: rgb(255, 199, 134);
}

.error {
  margin-right: 15px;
  font-size: 18px;
  font-weight: 600;
  color: rgb(211, 66, 4);
}


.user_card {
  width: 100%;
  padding: 30px 24px 40px;
  background-color: white;
  box-shadow:
    0 0 0 2px rgb(255, 255, 255),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  display: grid;
  grid-template-columns: 120px 1fr;
  gap: 25px 25px;

}

.user_card>*:nth-child(n + 2) {
  grid-column: 2 / span 1;
}

.user_title {
  display: grid;
  grid-template-columns: 1fr 105px;

}

body[data-theme="dark"] .user_title {
  color: white;
}

.info_title.empty,
.info_number.empty {
  color: rgb(166, 166, 166);
}

.statistic {
  background-color: rgb(255, 245, 237);
  padding: 15px 32px;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;

}

.statistic_info:hover {
  color: rgb(250, 107, 5);
  font-weight: 600;

}


.avatar {
  max-width: 120px;
  border-radius: 50%;
}


.user_info {
  background-color: rgb(255, 245, 237);
  padding: 15px 32px;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;

}

.info_item.empty {
  fill: rgb(166, 166, 166);
  color: rgb(166, 166, 166);
}

a {
  color: black;
}

.info_item a:hover {
  color: rgb(250, 107, 5);
}

.repos_container,
.followers_container,
.following_container {
  margin: 20px auto;
  width: 100%;
  padding: 40px 40px;
  background-color: white;
  box-shadow:
    0 0 0 2px rgb(255, 255, 255),
    0.3em 0.3em 1em rgba(0, 0, 0, 0.2);
  border-radius: 15px;
  position: relative;

}

.repos_item {
  padding: 15px 0;
  margin: 0 10px;
  border-top: 2px solid rgba(173, 168, 168, 0.5);
}


.repos_main_title,
.followers_main_title,
.followings_main_title {
  padding: 15px 0;
  text-align: center;
}

.repos_info {
  display: flex;
  justify-content: space-between;

}

.repos_status {
  border: 2px solid rgba(173, 168, 168, 0.5);
  padding: 2px 5px;
  border-radius: 15px;
}

.repos_link:hover {
  color: rgb(250, 107, 5);
}

.close_icon {
  position: absolute;
  margin-left: 735px;
  margin-top: -80px;
  fill: gray;
}

.close_icon:hover {
  fill: rgb(250, 107, 5);
}

.fade-in {
  opacity: 0;
  animation: fadeIn 0.5s forwards;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fade-out {
  opacity: 1;
  animation: fadeOut 0.5s forwards;
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }

  to {
    opacity: 0;
  }
}

.follower_item,
.following_item {
  padding: 10px 15px;
  margin: 4px 10px;
  background-color: rgb(255, 245, 237);
  border-radius: 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.follower_link:hover,
.following_link:hover {
  color: rgb(250, 107, 5);
}

.follower_avatar,
.following_avatar {
  max-width: 70px;
  border-radius: 50%;
}



/*dark theme*/

body[data-theme="dark"] {
  background-color: rgb(96, 87, 83);
}

body[data-theme="dark"] .header,
body[data-theme="dark"] .theme_name,
body[data-theme="dark"] .theme_icon,
body[data-theme="dark"] .error,
body[data-theme="dark"] .repos_link:hover,
body[data-theme="dark"] .close_icon:hover,
body[data-theme="dark"] .follower_link:hover,
body[data-theme="dark"] .following_link:hover,
body[data-theme="dark"] .info_item .link:hover {
  color: rgb(254, 129, 4);
  fill: rgb(254, 129, 4);
}

body[data-theme="dark"] .search,
body[data-theme="dark"] .user_card,
body[data-theme="dark"] .repos_container,
body[data-theme="dark"] .followers_container,
body[data-theme="dark"] .following_container {
  background-color: rgb(149, 137, 127);
  box-shadow:
    0 0 0 3px rgba(251, 198, 152, 0.5),
    0.3em 0.3em 1em rgba(217, 127, 18, 0.2);

}

body[data-theme="dark"] .repos_item {
  border-top: 2px solid rgba(251, 198, 152, 0.5);
}

body[data-theme="dark"] .input,
body[data-theme="dark"] .repos_container {
  background-color: rgb(149, 137, 127);
  color: white;
}

body[data-theme="dark"] .info,
body[data-theme="dark"] .input::placeholder,
body[data-theme="dark"] .search_icon,
body[data-theme="dark"] .close_icon,
body[data-theme="dark"] .followers_main_title,
body[data-theme="dark"] .followings_main_title,
body[data-theme="dark"] .follower_link,
body[data-theme="dark"] .following_link,
body[data-theme="dark"] .info_item .link {
  color: white;
  fill: white;
}

body[data-theme="dark"] .repos_status {
  border: 2px solid rgba(251, 198, 152, 0.5);
}

body[data-theme="dark"] .statistic,
body[data-theme="dark"] .user_info,
body[data-theme="dark"] .follower_item,
body[data-theme="dark"] .following_item {
  background-color: rgb(96, 87, 83);
  color: white;
  fill: white;
}

body[data-theme="dark"] .button {
  color: rgb(74, 66, 63);
}

body[data-theme="dark"] .button:hover {
  background-color: rgb(255, 150, 59);
}

body[data-theme="dark"] .repos_link {
  color: black;
}